#chat {
    position: relative;
}

/* serverError
 * the chat error overlay
*/
#chat .serverError {
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(255,255,255,0.75);
    z-index: 1;
}
#chat .serverError > div {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 50%;
    text-align: center;
    transform: translate3d(-50%,-50%,0);
    padding-left: 40px;
    font-weight: bold;
}

#chat .serverError > div:before {
    content: "";
    position: absolute;
    top: 50%;
    margin-top: -20px;
    left: 0;
    width: 40px;
    height: 40px;
    background-color: #000;

    border-radius: 100%;
    -webkit-animation: error-scaleout 1.5s infinite ease-in-out;
    animation: error-scaleout 1.5s infinite ease-in-out;
}

@-webkit-keyframes error-scaleout {
    0% { -webkit-transform: scale(0) }
    100% {
        -webkit-transform: scale(1.0);
        opacity: 0;
    }
}

@keyframes error-scaleout {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 100% {
    -webkit-transform: scale(1.5);
    transform: scale(1.5);
    opacity: 0;
  }
}

/* chatContent
 * the chat messages container
*/
div#chatContent{
    height:400px;
    overflow:auto;
    word-wrap:break-word;
    padding-right: 1em;
}

div#chatContent > div {
    position: relative;
}

div#chatContent > div:not(.chat-info) + div {
    margin-top: 1em;
}

div#chatContent > div:last-child {
    margin-bottom: 1em;
}

div#chatContent div.ownMessage {
    text-align: right;
}


/* chat-info information messages */
div#chatContent div.chat-info {
    font-size: 0.9em;
    font-weight: 200;
    text-align: center;
}


/* username styles */
div#chat a.username::before {
    text-decoration: underline;
}

div#chat a.username::before,
div#chat a.username:hover::before {
    text-decoration: none;
}

div#chatContent div.chat-info .username {
    font-weight: normal;
}

/* meta-information of messages (username, time,...) */
div#chatContent div .meta-information {
    margin: 0 2em;
}

div#chatContent div.admin_message .meta-information {
    margin: 0 2.5em;
}

div#chatContent div .meta-information .chatdate {
    opacity: 0.6;
    margin-left: 0.4em;
    font-size: 0.8em;
}

/* message content */
div#chatContent div .message_content {
    max-width: 80%;
    display: inline-block;
    text-align: left;
    background-color: lightgray;
    padding: 0.4em 0.6em;
    min-width: 8em;
    position: relative;
    margin-top: 0.3em;
}


div#chatContent div .message_content::before {
    content: "";
    width: 0px;
    height: 0px;
    border-top: 1em solid transparent;
    border-right: 1em solid lightgray;
    position: absolute;
    top: -0.9em;
}

div#chatContent div.ownMessage .message_content::before {
    border-top: 1em solid transparent;
    border-left: 1em solid #007bb1;
    border-right: none;
    right: 0.4em;
}


div#chatContent div.ownMessage .message_content {
    background-color: #007bb1;
    color: #fff;
}

div#chatContent div.ownMessage .message_content a {
    color: #fff;
    text-decoration: underline;
}

div#chatContent .message_content span.additional_content {
    display: block;
    font-size: 0.8em;
    font-weight: lighter;
    opacity: 0.8;
}

div#chatContent .message_content span.additional_content .username {
    font-weight: normal;
}

/* whisper icon besides private messages */
div#chatContent .whisper-icon {
    display: inline-block;
    float: left;
    background-image: url("img/mail.png");
    height: 30px;
    width: 30px;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 24px 5px;
    opacity: 0.5;
}

div#chatContent a.whisper-icon:hover {
    opacity: 1;
}

div#chatContent div.ownMessage .whisper-icon {
    float: right;
}

/* breakpoint definition for chat message inputs and chatUser button */
div#chatMsg textarea,
button#chatMsgSubmit,
div#chatUser {
    display: inline-block;
    width: calc(~"(620px - 100%) * 620");
    margin-bottom: 0.25em;
}


/* chatUser
 * the user list container
*/
div#chatUser {
    min-width: 15%;
    max-width: 30%;
    float: right;
}

div#chatUser button {
    width: 100%;
}

div#chatUser #userContainer{
    display: none;
    list-style:none;
    padding:0;
    margin:0;
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    background-color: white;
    border: 1px solid lightgray;
    padding: 1em;
    -moz-column-width: 11.5em;
    -webkit-column-width: 11.5em;
    column-width: 11em;
}

div#chatUser .username {
    word-wrap: break-word;
    display: inline-block;
    max-width: 100%;
    margin-top: 0.2em;
    margin-bottom: 0.2em;
}

div#chatUser .username.self {
    font-weight: bold;
}

/*
 * chat message inputs
*/
div#chatMsg {
    position: relative;
}

#chatMsgForm {
    display: inline;
}

#chatMsgForm #chatMsgTarget {
    position: absolute;
    padding: 0.3em 0.5em;
    margin: 6px 12px;
    color: white;
    background-color: #007bb1;
    padding-right: 2em;
}

#chatMsgForm #chatMsgTarget a {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0.5em;
}
#chatMsgForm #chatMsgTarget a::before {
    content: "X";
    color: white;
    opacity: 0.7;
    text-decoration: none;
    vertical-align: middle;
}
#chatMsgForm #chatMsgTarget a:hover::before {
    opacity: 1;
    text-decoration: none;
}

div#chatMsg textarea {
    min-width: 63%;
    max-width: 100%;
    min-height: 0;
    resize: none;
    vertical-align: middle;
}

#counter {
    width: 80px;
    margin-left: -80px;
    text-align: center;
    display: inline-block;
}

button#chatMsgSubmit {
    min-width: 20%;
    max-width: 68%;
    vertical-align: top;
}


.chat_explanation, .chat_nosession {
    padding-left: 0.5em;
    border-left: 0.5em solid #CDD4E2;
}



span.chat_option {
    font-weight: normal;
}

.password_field {
    display: none;
}

/* content from notification.css */

#notification_overlay{
    width            : 100%; /* will override by js */
    height           : 100%; /* will override by js */
    position         : fixed;
    top              : 0;
    left             : 0;
    z-index          : 300000;
    display          : none;
}

#notification_overlay .transparency{
    background-color : #000;
    width            : 100%; /* will override by js */
    height           : 100%; /* will override by js */
    position         : fixed;
    top              : 0;
    left             : 0;
    z-index          : -1;
    opacity          : 0.50;
}

#notification_container{
    position        : relative;
    width           : 500px;
    opacity         : 1.0;
    z-index          : 300001;
    word-wrap: break-word;
}

#notification_container hr {
    visibility: hidden;
}

#notification_container tt {
    font-family: monospace;
    font-weight: bold;
    color: #555;
    padding: 0px 5px 0px 5px;
}

#notification_container em {
    font-size: 1.6em;
    font-style:normal;
    margin:10px;
}

#notification_container input[type=text] {
    width:400px;
    border-radius: 3px;
    margin-left:10px;
}

.notification{
    width           : 500px;
    padding         : 20px;
    margin          : 5px 0 5px 0;
    border: 2px #555 solid;
    font-weight     : bold;
    background-color: #fff;
    z-index          : 300001;
}
.notification_button_container{
    float: right;
}
.notification_button{
    margin: 2px 10px 2px 10px;
    float: left;
}
